import React, { Component } from "react";
import { reduxForm } from "redux-form";
import { connect } from "react-redux";

import paginationFactory from "react-bootstrap-table2-paginator";
// import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import { berhasil, gagal } from "../Alert";
import uuid from "react-uuid";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPost, AxiosMasterPut } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import {
  getValidasiPO,
  getValidasiPOSelected,
} from "../actions/validasiPOAction";
import Swal from "sweetalert2";
import ReactTooltip from "react-tooltip";

const maptostate = (state) => {
  return {
    validasiPO: state.provinsi.validasiPO,
    validasiPOSelected: state.provinsi.validasiPOSelected,
  };
};

class validasiPO extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_preorder",
          text: "Nomor PO",
        },
        {
          dataField: "kode_lokasi",
          text: "Lokasi",
        },
        {
          dataField: "kode_sales",
          text: "Sales",
        },
        {
          dataField: "total_netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "disc_persen",
          text: "Disc (%)",
        },
        {
          dataField: "total_netto_disc_ext",
          text: "Netto - Disc Ext",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="mx-auto">
                <div className="row">
                  <div className="col-lg-6">
                    <button
                      data-tip
                      data-for="validasi"
                      className="btn btn-success mr-2"
                      data-tut="reactour_4"
                      onClick={() => this.kirimvalidasi(row)}
                    >
                      <i className="fa fa-check "></i>
                      <ReactTooltip id="validasi" type="dark" effect="solid">
                        <span>Validasi</span>
                      </ReactTooltip>
                    </button>
                  </div>
                  <div className="col-lg-6">
                    <button
                      data-tip
                      data-for="cancel"
                      className="btn btn-danger mr-2"
                      data-tut="reactour_4"
                      onClick={() => this.cancelValidasi(row.no_preorder)}
                    >
                      <i className="fa fa-times "></i>
                      <ReactTooltip id="cancel" type="dark" effect="solid">
                        <span>Cancel</span>
                      </ReactTooltip>
                    </button>
                  </div>
                </div>
              </div>
            );
          },
        },
      ],

      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      angka: 0,
    };
  }

  componentDidMount() {
    localStorage.setItem("FakturTerpilih", "[]");
    localStorage.setItem("FakturTerpilih_kirim", "[]");
    this.props.dispatch(getValidasiPO());
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getValidasiPOSelected());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };

  cancelValidasi(data) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Cancel Data PO Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        AxiosMasterPut("pre-order/cancel/" + data)
          .then(() => berhasil("Berhasil Cancel Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiPO()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
      }
    });
  }

  kirimvalidasi(data) {
    data.detail_barang.map((data) => {
      delete data._id;
      delete data.kode_kategori;
      delete data.nama_jenis;
      return true;
    });
    delete data.status_valid;
    delete data.validate_by;
    delete data.validate_date;
    delete data.input_by;
    delete data.input_date;
    delete data.__v;
    AxiosMasterPost("validate-pre-order/simpan", data)
      .then(() => berhasil("Berhasil Validasi"))
      .then(() => this.prevStep())
      .then(() => this.props.dispatch(getValidasiPO()))
      .then(() => localStorage.removeItem("FakturTerpilih"))
      .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
      .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Validasi Barang" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                {/* <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div> */}
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="no_preorder"
                        data={this.props.validasiPO}
                        columns={this.state.columns}
                        search
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                        // selectRow={selectRow}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField={uuid()}
                        data={this.props.validasiPOSelected}
                        columns={this.state.columns}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className=" col-lg-6 text-left">
                          <button
                            className="btn btn-primary"
                            onClick={() => this.prevStep()}
                          >
                            <i className="fa fa-chevron-left mr-3"></i>
                            BACK
                          </button>
                        </div>
                        <div className=" col-lg-6 text-right">
                          <button
                            className="btn btn-primary"
                            onClick={() =>
                              this.kirimvalidasi(this.state.tipeTransaksi)
                            }
                          >
                            <i className="fa fa-check mr-3"></i>
                            VALIDASI
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiPO = reduxForm({
  form: "validasiPO",
  enableReinitialize: true,
})(validasiPO);
export default connect(maptostate, null)(validasiPO);
